import {getAuthMeThunkCreator} from "./auth-reducer";
import {getThemeThunkCreator} from "./theme-reducer";
import {GlobalStateType, InferActionsTypes} from "./store-redux";
import {ThunkAction} from "redux-thunk";
import {ComThunkTp} from "../types/commonTypes";

const SET_INITIALISED_APP = "myApp/app-reducer/SET_INITIALISED_APP"; //  
const APP_INITIAL_STATE = "myApp/app-reducer/APP_INITIAL_STATE"; //   
const SET_PATCH = "myApp/app-reducer/SET_PATCH"; //    URL
const SET_PAGE_WIDTH = "myApp/app-reducer/SET_PAGE_WIDTH"; //   

export const AppActions = {
    setInitialisedApp: () => { //     
        return {type: SET_INITIALISED_APP} as const
    },

    appInitialState: () => { //    
        return {type: APP_INITIAL_STATE} as const
    },

    setPatch: (patch: string) => { //    
        return {type: SET_PATCH, patch} as const
    },

    setPageWidth: (PageWidth: number) => { //    
        return {type: SET_PAGE_WIDTH, PageWidth} as const
    }
}

type AppActionTypes = InferActionsTypes<typeof AppActions>

type initialStateType = typeof initialState

let initialState = { //     
    initialisedApp: false, //   ?
    patch: "", //    URL
    PageWidth: document.documentElement.scrollWidth, //    
    MobileWidth: 620,

}

let appReducer = (state: initialStateType = initialState, action: AppActionTypes): initialStateType => {//  
    let stateCopy: initialStateType; //      
    switch (action.type) {
        case SET_INITIALISED_APP: //   
            stateCopy = {
                ...state, //   
                initialisedApp: true, //      true
            }
            return stateCopy; //     
        case APP_INITIAL_STATE: //    
            stateCopy = initialState
            return stateCopy; //     
        case SET_PATCH: //   
            stateCopy = {
                ...state, //   
                patch: action.patch, //      true
            }
            return stateCopy; //     
        case SET_PAGE_WIDTH: //    
            stateCopy = {
                ...state, //   
                PageWidth: action.PageWidth, //      true
            }
            return stateCopy; //     
        default:
            return state; //     
    }
}

export const initialisedAppThunkCreator = (): ComThunkTp<AppActionTypes> => {//   
    return (dispatch, getState) => { //    
        const promise1 = dispatch( getAuthMeThunkCreator() ) //   
        const promise2 = dispatch( getThemeThunkCreator() ) //  
        Promise.all( [promise1, promise2] ) //    
            .then( () => {
                dispatch( AppActions.setInitialisedApp() ) //     true
            } )
    };
}

export default appReducer;










