import {applyMiddleware, combineReducers, compose, createStore} from "redux";
import thunkMiddleWare from 'redux-thunk'; // thunkMiddleWare        
import profileReducer from "./profile-reducer";
import dialogsReducer from "./dialogs-reducer";
import usersReducer from "./users-reducer";
import authReducer from "./auth-reducer";
import appReducer from "./app-reducer";
import themeReducer from "./theme-reducer";
import feedBackReducer from "./feedback-reducer"
import tasksReducer from "./tasks-reducer";
import dialod2Reducer from "./dialog2-reducer"

let reducers = combineReducers({ //       store
    profilePage: profileReducer, //  
    dialogsPage: dialogsReducer, //  
    usersPage: usersReducer, //   
    auth: authReducer, //   
    app: appReducer, //   
    theme: themeReducer, //   (dark-light)
    feedback: feedBackReducer, //  
    tasks: tasksReducer,//  
    dialog2: dialod2Reducer //  2
});

// @ts-ignore
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const store = createStore(reducers, composeEnhancers(
  applyMiddleware(thunkMiddleWare)
));

type reducersType = typeof reducers
export type GlobalStateType = ReturnType<reducersType> //   

//https://habr.com/ru/companies/alfa/articles/452620/ + https://www.youtube.com/watch?v=2yJXFMqEbJs&list=PLcvhF2Wqh7DM3z1XqMw0kPuxpbyMo3HvN&index=9&t=870s
//,     ,     ActionCreator   
type PropertiesTypes<T>= T extends {[key:string]: infer U}? U : never
//   T =   T   {},    key   (setStatus)
//  setStatus: (newStatus: string) => { return {type: SET_STATUS, newStatus} as const},
//      (infer U)    ,     (never)

export type InferActionsTypes<T extends {[key:string]: (...args: any) => any} > = ReturnType<PropertiesTypes<T>>
//      (U).  ,  T  ,    ,
//    ,        ,    .
//   ,     (     ),    
// ( )     .     

// @ts-ignore
window.store = store; //     

export default store
