import {apiCommon} from "../components/api/apiLocalStorage";
import {Dispatch} from "redux";
import {GlobalStateType, InferActionsTypes} from "./store-redux";

const SET_THEME = "myApp/dark-light-reducer/SET_THEME"; //  

export const ThemeActions = {
    setTheme: (themeUpdate: "light" | "dark") => { //   
        return {type: SET_THEME, themeUpdate} as const
    }
}


type ThemeActionTypes = InferActionsTypes<typeof ThemeActions>

const initialState = { //   
    themeBLL: "light" as "light" | "dark", //   bll  
}
type initialStateType = typeof initialState

const themeReducer = (state: initialStateType = initialState, action: ThemeActionTypes): initialStateType => {//  
    let stateCopy: initialStateType; //      
    switch (action.type) {
        case SET_THEME: //   
            stateCopy = {
                ...state, //   
                themeBLL: action.themeUpdate, //    BLL
            }
            return stateCopy; //     
        default:
            return state; //     
    }
}

export const setThemeThunkCreator = (theme1: "light" | "dark") => {//    LocalStorage
    return async (dispatch: Dispatch<ThemeActionTypes>, getState: () => GlobalStateType) => { //     LocalStorage
        const response1 = await apiCommon.putTheme1( theme1 )  //    localStorage
        if (response1) {
            dispatch( ThemeActions.setTheme( response1 ) )  //   localStorage    store
        }

    }
}
export const getThemeThunkCreator = () => {//    LocalStorage
    const getThemeThunk = async (dispatch: Dispatch<ThemeActionTypes>, getState: () => GlobalStateType) => { //     LocalStorage
        const response1 = await apiCommon.getTheme1()  //    localStorage
        if (response1) {
            dispatch( ThemeActions.setTheme( response1 ) )  //   localStorage    store
        }

    }
    return getThemeThunk
}


export default themeReducer;










