import GetDate from "../common/ErrorBoundary/GetDate";

export const apiDialogs = { //    api  DialogList
  _setMessages2LS: (Dialog_2, myId, userId) => { //      LocalStorage    
    const LocalStoragedialogUpdateTime1 =
      GetDate().Day + "." +
      GetDate().Month + "" +
      GetDate().Year + " " +
      GetDate().Hour + ":" +
      GetDate().Minutes + ":" +
      GetDate().Seconds

    //        

    const dialogNameLocal = myId > userId ? "Dialog_" + myId + "_" + userId : "Dialog_" + userId + "_" + myId;  //     
    const dialogUpdateTimeLocal = myId > userId ? "Dialog_" + myId + "_" + userId + "_UpdateTime" : "Dialog_" + userId + "_" + myId + "_UpdateTime";//       

    localStorage.setItem(dialogNameLocal, JSON.stringify(Dialog_2)); //     LocalStorage
    localStorage.setItem(dialogUpdateTimeLocal, JSON.stringify(LocalStoragedialogUpdateTime1)); //      LocalStorage

    const Dialog_3 = apiDialogs.getDialog(myId, userId); //     LocalStorage

    return Dialog_3 //   
  },
  getDialog: (myId, userId) => {
    const dialogNameLocal = myId > userId ? "Dialog_" + myId + "_" + userId : "Dialog_" + userId + "_" + myId; //     
    let LocalStorageDialogs1 = JSON.parse(localStorage.getItem(dialogNameLocal)); //    LocalStorage   
    if (!LocalStorageDialogs1) { //       (undefined)
      LocalStorageDialogs1 = []; //    
    }
    return LocalStorageDialogs1 //     Dialog_25528_1079
  },
  getUpdateTime: (myId, userId) => {
    const dialogUpdateTimeLocal = myId > userId ? "Dialog_" + myId + "_" + userId + "_UpdateTime" : "Dialog_" + userId + "_" + myId + "_UpdateTime"; //       
    let LocalStoragedialogUpdateTime1 = JSON.parse(localStorage.getItem(dialogUpdateTimeLocal)); //       
    if (!LocalStoragedialogUpdateTime1) { //        (undefined)
      LocalStoragedialogUpdateTime1 = null; //    
    }
    return LocalStoragedialogUpdateTime1 //     
  },

  deleteMessage: (messageID, myId, userId) => {
    const Dialog_1 = apiDialogs.getDialog(myId, userId); //   Dialog_25528_1079  LocalStorage
    const Dialog_2 = Dialog_1.filter(message => message.id !== messageID) //  ,     

    const Dialog_3 = apiDialogs._setMessages2LS(Dialog_2, myId, userId)//      (LocalStorage)


    if (Dialog_3.length===0) { //         

      //       LocalStorage
      const dialogListUserId1 = "DialogList_" + userId //   DialogList
      let Data1 = JSON.parse(localStorage.getItem(dialogListUserId1)); //       
      if (!Data1) { //     
        Data1 = [] //      ,  
      }
      const Data2 = Data1.filter(d=>d.userId!==myId) //      ,  ,  0 , 
      localStorage.setItem(dialogListUserId1, JSON.stringify(Data2)); //   LocalStorage  

      //       LocalStorage
      const dialogUpdateTimeLocal = myId > userId ? "Dialog_" + myId + "_" + userId + "_UpdateTime" : "Dialog_" + userId + "_" + myId + "_UpdateTime"; //       
      localStorage.removeItem(dialogUpdateTimeLocal); //     ,      

      //    Dialog_25528_1079  LocalStorage
      const dialogNameLocal = myId > userId ? "Dialog_" + myId + "_" + userId : "Dialog_" + userId + "_" + myId;  //     
      localStorage.removeItem(dialogNameLocal); //    Dialog_25528_1079  LocalStorage,      
    }

    return Dialog_3 //     
  },

  updateDialogListUserId: (userId1, userId2, Name2, Photo2) => { //      ,      
    const dialogListUserId1 = "DialogList_" + userId1 //   DialogList
    let Data1 = JSON.parse(localStorage.getItem(dialogListUserId1)); //       
    if (!Data1) {
      Data1 = [] //      ,  
    }
    const newDialogLocal = { //    
      dialogId: Data1.length + 1, // id   dialogList
      userId: userId2, // Id     DialogList
      userName: Name2, //      DialogList
      userPhoto: Photo2 //      DialogList
    }

    let shouldDataUpdate = true; //

    Data1.map((d1) => { //      ,       ,    
      if (d1.userId === userId2) {
        shouldDataUpdate = false;
        return null
      }
      return null
    })

    if (shouldDataUpdate) { //     true,     
      const Data2 = [...Data1, newDialogLocal];
      localStorage.setItem(dialogListUserId1, JSON.stringify(Data2)); //    LocalStorage
    }
  },

  getDialogListMyID: (myId) => { //     ,   
    const dialogListUserId1 = "DialogList_" + myId //    DialogList
    let Data1 = JSON.parse(localStorage.getItem(dialogListUserId1)); //     
    if (!Data1) {
      Data1 = [] //      ,  
    }
    return Data1 //   
  },

  postDialog: (formDataNewMessage, myId, MyName, MyPhoto, userId) => { //   LocalStorage

    const Dialog_1 = apiDialogs.getDialog(myId, userId); //   Dialog_25528_1079  LocalStorage

    const Dialog_2 = [...Dialog_1, {//      
      id: Dialog_1.length + 1, //  id 
      Date: GetDate(), //   
      userId: myId, //    
      message: formDataNewMessage //    
    }]
    const Dialog_3 = apiDialogs._setMessages2LS(Dialog_2, myId, userId)//      (LocalStorage)

    apiDialogs.updateDialogListUserId(userId, myId, MyName, MyPhoto)
    //  DialogList ,  .    ,    

    //    DialogList,         .

    return Dialog_3 //     DAL  BLL

  },

  deleteDialog: (dialogId, userId1, userId2) => { //       ID
    const Data1 = apiDialogs.getDialogListMyID(userId1)
  // console.log(Data1)
    const Data2 = Data1.filter(d=>d.dialogId!==dialogId)
    //console.log(Data2)
    localStorage.setItem("DialogList_" + userId1, JSON.stringify(Data2)); //    LocalStorage

  },

}

export const apiCommon = { //    api   
  putTheme1: (theme1) => { //  theme1  localStorage
    if (!theme1) {
      theme1="light" //     
    }
    localStorage.setItem("theme1", JSON.stringify(theme1)); //  theme1  LocalStorage
    const Data1 = apiCommon.getTheme1(); //    localStorage  
    return Data1 //   
  },
  getTheme1: () => { //  theme1  localStorage
    let Data1 = JSON.parse(localStorage.getItem("theme1")); //  theme1  LocalStorage
    if (!Data1) {
      Data1="light" //     
      apiCommon.putTheme1(Data1) //      localStorage   
    }
    return Data1 //    
  },
}
