import {apiDialogs} from "../components/api/apiLocalStorage";
import {GlobalStateType, InferActionsTypes} from "./store-redux";
import {ThunkAction} from "redux-thunk";
import {ComThunkTp, dialogs2Type, messages2Type, NulableType} from "../types/commonTypes";

const DIALOGS_INITIAL_STATE = "myApp/dialogs-reducer/DIALOGS_INITIAL_STATE";  //   
const SET_MESSAGES = "myApp/dialogs-reducer/SET_MESSAGES";  //     
const DIALOG_USER_ID = "myApp/dialogs-reducer/DIALOG_USER_ID"; //  ID ,   
const DIALOG_LAST_UPDATE_TIME = "myApp/dialogs-reducer/DIALOG_LAST_UPDATE_TIME"; //  ID ,   
const DIALOG_USER_FOLLOWED = "myApp/dialogs-reducer/DIALOG_USER_FOLLOWED"; //  follow/unfollow   (  )
const GET_MY_DIALOG_LIST = "myApp/dialogs-reducer/GET_MY_DIALOG_LIST"; //   

export const DialogsActions = {
    dialogsInitialState: () => { //    
        return {type: DIALOGS_INITIAL_STATE} as const
    },

    setMessages: (updatedMessages: Array<messages2Type>) => { //       messages2
        return {type: SET_MESSAGES, updatedMessages} as const
    },

    setdialogUserID: (dialogUserID: number) => { //       messages2
        return {type: DIALOG_USER_ID, dialogUserID} as const
    },

    setDialogLastUpdateTime: (dialogLastUpdateTime: string) => { //       
        return {type: DIALOG_LAST_UPDATE_TIME, dialogLastUpdateTime} as const
    },

    setDialogUserFollowed: (dialogUserFollowed: boolean) => { //       
        return {type: DIALOG_USER_FOLLOWED, dialogUserFollowed} as const
    },

    getMyDialogList: (myDialogList: Array<dialogs2Type>) => { //      
        return {type: GET_MY_DIALOG_LIST, myDialogList} as const
    }

}

type DialogsActionTypes = InferActionsTypes<typeof DialogsActions>

let initialState = { //    
    messages2: null as NulableType<Array<messages2Type>>, //    ,      Array<messages2Type>
    dialogUserID: 0 as number, // ID    
    dialogUserFollowed: false, //  followed? ()
    dialogLastUpdateTime: "" as string, //     
    dialogs2: null as NulableType<Array<dialogs2Type>> //  .    LocalStorage    
}

type initialStateType = typeof initialState

let dialogsReducer = (state: initialStateType = initialState, action: DialogsActionTypes): initialStateType => { //  
    let stateCopy: initialStateType; //      
    switch (action.type) {
        case DIALOGS_INITIAL_STATE: //        
            stateCopy = initialState;
            return stateCopy
        case SET_MESSAGES: //        
            stateCopy = {
                ...state,
                messages2: action.updatedMessages, //  
            }
            return stateCopy
        case DIALOG_USER_ID: //    ID ,   
            stateCopy = {
                ...state,
                dialogUserID: action.dialogUserID, //  ID ,   
            }
            return stateCopy
        case DIALOG_LAST_UPDATE_TIME: //        
            if (state.dialogLastUpdateTime !== action.dialogLastUpdateTime) { // ,        (  )
                stateCopy = {
                    ...state,
                    dialogLastUpdateTime: action.dialogLastUpdateTime, //       
                }
                return stateCopy
            }
            return state //    ,    
        case DIALOG_USER_FOLLOWED: //    follow/unfollow   (   )
            stateCopy = {
                ...state,
                dialogUserFollowed: action.dialogUserFollowed, //   follow/unfollow   (   )
            }
            return stateCopy
        case GET_MY_DIALOG_LIST: //     
            stateCopy = {
                ...state,
                dialogs2: action.myDialogList, //    
            }
            return stateCopy
        default:
            return state;
    }
}

export const getDialogsThunkCreator = (myId: number, userId: number): ComThunkTp<DialogsActionTypes> => {//    
    return async (dispatch, getState) => {//    
        const updatedMessages = await apiDialogs.getDialog( myId, userId )
        dispatch( DialogsActions.setMessages( updatedMessages ) )
    }
}
export const sendDialogsThunkCreator =
    (formDataNewMessage: string, myId: number, MyName: string, MyPhoto: string, userId: number): ComThunkTp<DialogsActionTypes> => {//     
        return async (dispatch, getState) => {//      
            const updatedMessages = await apiDialogs.postDialog( formDataNewMessage, myId, MyName, MyPhoto, userId )
            dispatch( DialogsActions.setMessages( updatedMessages ) )
        }
    }

export const getDialogLastUpdateTimeTnkCrt = (myId: number, userId: number): ComThunkTp<DialogsActionTypes> => {//    
    return async (dispatch, getState) => {//    
        const dialogLastUpdateTime = await apiDialogs.getUpdateTime( myId, userId ) //     
        dispatch( DialogsActions.setDialogLastUpdateTime( dialogLastUpdateTime ) ) //   BLL     
    }
}

export const deleteMessageThunkCreator = (messageID: number, myId: number, userId: number): ComThunkTp<DialogsActionTypes> => {//    
    return async (dispatch, getState) => {//     
        const dialogAfterDeleteMessage = await apiDialogs.deleteMessage( messageID, myId, userId ) //         
        dispatch( DialogsActions.setMessages( dialogAfterDeleteMessage ) )//      
    }
}


export const getMyDialogListThunkCreator = (myId: number): ComThunkTp<DialogsActionTypes> => {//   
    return async (dispatch, getState) => {// 
        const myDialogList = await apiDialogs.getDialogListMyID( myId ) //   
        dispatch( DialogsActions.getMyDialogList( myDialogList ) )//     
    }
}

//updateDialogListThunkCreator(myId, response.userId, response.fullName, response.photos.small
